import React, { useState } from "react";
import Button from "../../components/Buttons/Button";
import { FC_ResetSchoolSubject } from "../../actions";
import Alert from "../../components/Alert/Alert";

type AlertType = {
  target: "success" | "error";
  msg: string;
};

export const DashboardResetSubjectTeacher = () => {
  const [loading, setLoading] = useState(false);
  const [alert, setAlert] = useState<AlertType | null>(null);

  const resetSubjects = () => {
    if (window.confirm("Are you sure you wan't to perform this action?")) {
      setLoading(true);

      FC_ResetSchoolSubject((status, err) => {
        if (status) {
          setAlert({
            target: "success",
            msg: "Action performed successfully",
          });
        } else {
          setAlert({
            target: "error",
            msg: err,
          });
        }
        setLoading(false);
      });
    }
  };
  return (
    <>
      {alert?.target === "success" && (
        <Alert theme="success">{alert.msg}</Alert>
      )}
      {alert?.target === "error" && <Alert theme="danger">{alert.msg}</Alert>}
      <div className="py-2 px-4 ">
        <div className="container mx-auto p-4 bg-white">
          <h2 className="text-4xl font-bold mb-2">Reset Subject for teacher</h2>
          <p className="text-lg bg-text-600">
            This will force the headteachers to add the information of the
            subject <br />
            on every teacher before they submit the placement report
          </p>
          <div className="pt-4">
            <Button loading={loading} onClick={resetSubjects} size="lg">
              Force information update
            </Button>
          </div>
        </div>
      </div>
    </>
  );
};
